let c, ctx, time = 0;
let pnoise = [];
let museum, gallery, intervalId = 0;
let slideshow, index=false;

export default class SplashScreen{
    init(canvas){
        c = canvas;
        ctx = c.getContext("2d");

        c.width = window.innerWidth-5;
        c.height = window.innerHeight;

        museum = new Image();
        museum.src = require('../resources/gallery4.jpg');
        gallery = new Image();
        gallery.src = require('../resources/galery4.jpg');
        slideshow = {0:museum, 1:gallery};
        this.gameLoop = this.gameLoop.bind(this);
        
        museum.onload = () => {
            ctx.imageSmoothingEnabled = false;
            ctx.drawImage(museum, 0, 0, c.width, c.height);
            intervalId = setInterval(this.gameLoop, 33);
        }

        for(let i=0;i<1000;i++){
            pnoise[i] = Math.random()*1.5-0.3;
        }

        
    }
    restarting(){
        index = !index;
        time=0;
        this.unmount();
        intervalId = setInterval(this.gameLoop.bind(this), 33);
    }
    noise(t) {
        t=t%200;
        return this.lerp(pnoise[Math.floor(t)], pnoise[Math.ceil(t)], t-Math.floor(t));
    }
    lerp(a, b, t ) {
        return a * ( 1 - t ) + b * t;
    }
    gameLoop(){
        ctx.globalAlpha = 1;
        ctx.globalCompositeOperation="source-over";
        ctx.fillStyle = "white"; 
        ctx.clearRect(0, 0, c.width, c.height); 

        ctx.beginPath();
        for(let i=0;i<50;i++){
            ctx.lineTo(this.noise(time/1.5 + i * 2.2)*c.width, this.noise(time/1.5 + 500 + i * 2.2)*c.height);
        }
        ctx.fillStyle = "black"; 
        ctx.globalAlpha = Math.max(0, (time*3));
        ctx.fill();

        ctx.globalCompositeOperation="source-in";
        let num = Math.max(0.5-time/2, 0.1)*30;
            ctx.drawImage(slideshow[Number(!index)], -num, -num, c.width+num, c.height+num);
        

        ctx.globalCompositeOperation = "destination-over";

        ctx.globalAlpha = Math.max(0, 1-time*10);
        ctx.drawImage(slideshow[Number(index)], 0, 0, c.width, c.height);
            
        ctx.globalAlpha = Math.min(1, time*10);
        ctx.drawImage(slideshow[Number(!index)], 0, 0, c.width, c.height);
        
        ctx.globalCompositeOperation="source-over";

        if(time > 4) this.unmount();

        time+=0.01;
    }
    unmount(){
        clearInterval(intervalId);
    }
}