import React from 'react'
import '../../styles/forms.css'
import observer from '../../helpers/observer'
import requester from '../../helpers/requester'
import Header from '../Header'
import nail from '../../resources/nail.png'
import {Link, Redirect} from 'react-router-dom'

export default class Login extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            username: '',
            password: '',
            display: true,
            redirect: false
        };

        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(event){
        let name = event.target.name;
        let val = event.target.value;

        this.setState({
            [name]: val
        });
    }
    onSubmit(event){
        event.preventDefault();
        if(!this.state.password || !this.state.username){
            observer.fire('notifications', {
                type: 'error',
                message: 'Please fill all of the fields'
            })
        } else {
            requester.post(this.state, '/login')
                .then(res => res.json())
                .then(res => {
                    if(res.type === 'info') {
                        localStorage.setItem('username', res.username);
                        observer.fire('logging', true);
                        this.setState({redirect: true});
                    }
                    observer.fire('notifications', {
                        type: res.type,
                        message: res.message
                    });
                });
        }

    }
    render(){
        if (this.state.redirect) {
          return <Redirect to='/dashboard'/>;
        }

        return (
            <div className='backg'>
                <Header transparent={true}></Header>
                <img src={nail} className='nail'></img>

                <div className='form-container'>
                    <div >
                        <h2 className='form-title'></h2>
                    </div>
                    <div>
                        <form onSubmit={this.onSubmit}>
                            <span></span>
                            <input type='text' name='username' placeholder='username' onChange={this.onChange} />
                            <span></span>
                            <input type='password' name='password' placeholder='password' onChange={this.onChange} />
                            <hr></hr>
                            <button type='submit' className='submitbtn'></button>
                        </form>
                        
                        <h4 className='form-title'>...  <Link to='register'></Link></h4>
                        
                    </div>
                </div>
            </div>
        );
    }
}